* {
  box-sizing: border-box;
}

.conContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.container {
  overflow: scroll;
  border-radius: 7px;
  background-color: tomato;
  padding: 10px;
  height: 70%;
  width: 1300px;
  min-width: 860px;
  display: grid;
  gap: 3px;
  grid-template-columns: 1fr 2fr;
  div:first-child {
    background-color: white;
    width: 100%;
    height: 100%;
    overflow: hidden;
    img {
      object-fit: cover;
    }
  }
  div:nth-child(2) {
    img {
      height: 100%;
      width: 100%;
    }
  }
  > div:last-child {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    color: white;
    padding: 10px;
    h2 {
      font-size: 30px;
      margin-bottom: 0px;
    }
    div {
      display: flex;
      gap: 10px;
    }
    p {
      background-color: white;
      color: tomato;
      padding: 20px 5px;
      text-align: center;
      font-weight: bold;
      border-radius: 10px;
      line-height: 30px;
    }
    hr {
      width: 100%;
      color: rgba(255, 255, 255, 0.144);
    }
  }
}

.rate span{
  background-color: white;
  color: tomato;
  padding: 5px;
  border-radius: 10px;
}

.btn button{
  border: none;
  padding: 5px;
  border-radius: 2px;
  background-color: white;
  a {
    color: tomato;
    font-size: 20px;
    text-decoration: none;
  }
}