* {
  box-sizing: border-box;
}

.container {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 10px;
  > div {
    display: flex;
    flex-direction: column;
    // align-items: center;
    // justify-content: ;
    background-color: tomato;
    padding: 10px;
    border-radius: 5px;
    width: min-content;
    display: grid;
    grid-template-rows: max-content 1fr 0.9fr;
    > * {
      margin-bottom: 0px;
    }
    h2 a {
      color: white;
      text-decoration: none;
    }
    ul {
      color: rgba(255, 255, 255, 0.5);
    }
    &:hover {
      background-color: rgb(255, 255, 255);
      box-shadow: -3px 3px 20px rgba($color: #000000, $alpha: 0.5);
      transition: 0.4s;
      * {
      color: tomato;
      }
      div {
        width: 230px;
        height: 345px;
        overflow: hidden;
      }
      img {
        transform: scale(1.1);
        transition: 0.4s;
      }
    }
  }
}
